import React from 'react';
import './Services.css';
import ServicesSlider from '../ServicesSlider/ServicesSlider';
import ServiceCard from '../ServiceCard/ServiceCard';

const Services = () => {
    return(
        <section>
            <div className="banner">
                <p className="heroText">Our Services</p>
            </div>
            <div className="cardContainer imgServices">
                <ServicesSlider cl="sContainer">
                    <ServiceCard heading="Criminal Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Civil Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Family Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Constitutional Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Service Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="NRI Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Consumer Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Taxation Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Real Estate Matters" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Arbitration Services" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Alternative Dispute Resolution" details="Suggestions required if logo or image related to header to be put in?"/>
                    <ServiceCard heading="Legal Drafting" details="Suggestions required if logo or image related to header to be put in?"/>
                </ServicesSlider>
            </div>
        </section>
    )
}

export default Services;