import React from "react";
import Slider from "react-slick";
import "./ServicesSlider.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";


const ServicesSlider = ({dots=true,cl="na", time=2000,children}) => {
  const settings = {
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: time,
    dots: dots
  };

  return(
    <div className={cl}>
      <Slider {...settings}>
        {children}
      </Slider>
    </div>
  )
}


export default ServicesSlider;