import React from 'react';
import "./JoinUs.css";

const JoinUs = () => {
    return(
        <section className="marTop">
            <div className="banner">
                <p>Work with us</p>
            </div>
            <div className="containerBox">
                <div className="joinBoxLeft">
                    <p>Join Us</p>
                    <p>
                        We are currently recruiting trainees for intenship from different lawschools in India and abroad. 
                        Successful candidates will be offered internship and maybe recruited subject to their performance. 
                        The interns will be paid basic remuniration and bonuses based on their skills and experience.
                    </p>
                </div>
                <div style={{'width':'50%'}}>
                    <form className="formStyle">
                        <input className="formInput" type="text" placeholder="Name"/>
                        <input className="formInput" type="text" placeholder="Phone"/>
                        <input className="formInput" type="email" placeholder="Email"/>
                        <input className="formInput" type="textarea" placeholder="About Yourself"/>
                        <button className="formBtn" type="submit">send</button>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default JoinUs;