import React from 'react';
import "./Footer.css";
import {Link} from 'react-router-dom';
import Logo from '../Logo/Logo';

const Footer = () => {
    return(
        <section className="footerContainer">
            <div className="footerAddress">
                <ul>
                    <li className="footerLogo">
                        <Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/"><Logo/></Link>
                    </li>
                    <li>
                        Address:  111-000 address road, Chandigarh
                    </li>
                    <li>
                        Phone: +91-9991114445
                    </li>
                    <li>
                        Email: admin@indianlegalmatters.com
                    </li>
                    <li>
                        Links for social sites
                    </li>
                </ul>
            </div>
            <div className="footerServices">
                <ul>
                    <li className="footerHeading">
                        Services
                    </li>
                    <li>
                        Criminal Law
                    </li>
                    <li>               
                        Civil Law
                    </li>
                    <li>          
                        Family Matters
                    </li>
                    <li>         
                        Service Matters
                    </li>
                    <li>
                        Consultation
                    </li>
                </ul>
            </div>
            <div className="footerLinks">
                <ul>
                    <li className="footerHeading">Links</li>
                    <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/">Home</Link></li>
                    <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/about">About US</Link></li>
                    <li>Blog</li>
                    <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/join">Join Us</Link></li>
                    <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/contact">Contact Us</Link></li>
                    <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/colab">Our Collaborations</Link></li>
                </ul>
            </div>
            <div className="footerConsultation">
                <ul>
                    <li className="footerHeading">
                        Consultations
                    </li>
                    <li>
                        Call now for free consultations:
                    </li>
                    <li>
                        +91 - 9465784965
                    </li>
                </ul>
            </div>
            <p className="designedBox"></p>
        </section>
    )
}

export default Footer;