import React from 'react';
import './Partners.css';
import ServicesSlider from '../ServicesSlider/ServicesSlider';
import p1 from '../../imgs/collab/indoglobal.png';
import p2 from '../../imgs/collab/singh.png';
import p3 from '../../imgs/collab/legis.png';
import p4 from '../../imgs/collab/sofat.png';


const Partners = () => {
    return(
        <section>
            <div className="banner">
                <p className="heroText">Our Collaboration</p>
            </div>
            
            <div className="cardContainer">
                <ServicesSlider dots={false} time={3000}>
                    <a className="partner" target="blank" style={{ color: 'inherit',textDecoration: 'inherit' }} href="http://www.indogloballawfirm.com/">
                        <img src={p1} alt="indo global"/>
                    </a>
                    <a className="partner" target="blank" style={{ color: 'inherit',textDecoration: 'inherit' }} href="http://www.singhlawchambers.com/">
                        <img src={p2} alt="singh"/>
                    </a>
                    <a className="partner" target="blank" style={{ color: 'inherit',textDecoration: 'inherit' }} href="http://www.legischamber.com/">
                        <img src={p3} alt="legis"/>
                    </a>
                    <a className="partner" target="blank" style={{ color: 'inherit',textDecoration: 'inherit' }} href="http://sofatassociates.com/">
                        <img src={p4} alt="sofat"/>
                    </a>
                </ServicesSlider>
            </div>
        </section>
    )
}

export default Partners;