import React from 'react';
import "./ContactUs.css"

const ContactUs = () => {
    return(
        <section  className="marTop">
            <div className="banner">
                <p>Reach out to us</p>
            </div>
            <div className="containerBox">
                <div className="addressBox">
                    <div className="addressBoxLabel">office 1</div>
                    <p className="addressHeader">Address</p>
                    <p>123 address road, Chandigarh - 143100</p>
                    <p className="addressHeader">Contact</p>
                    <p>+91 - 9154654878</p>
                </div>
            </div>
        </section>
    )
}

export default ContactUs;