import React from 'react';
import './PopUp.css'

const PopUp = ({closePopup}) => {
    return(
        <div className="overlay">
            <div className="popupContainer">
                <p>DISCLAIMER</p>
                <p className="pText">
                    The Bar Council of India does not authorize and permit any sought of commercial advertisement or solicitation work done byﾠ
                    advocatesﾠin any form or manner. By accessing this website (www.indianlegalmatters.com), you acknowledge and confirm that you are
                    in quest and seeking information relating toﾠIndian Legal Matters of your own accord and that there has been no form of solicitation, 
                    commercial advertisement, encouragement or inducement done byﾠIndian Legal Matters or the people associated to it. 
                    This web-site http://www.indianlegalmatters.com ("Website") is only a public resource for general information aboutﾠ
                    Indian Legal Matters. It does not make any effort or intent to implore new clients or engagements through this website.
                </p>
                <div className="buttons">
                    <button onClick={closePopup} className="popupBtn">Accept</button>
                    <a href="https://www.google.com/" alt="google">
                        <button  className="popupBtn">Decline</button>
                    </a>
                </div>
            </div>
        </div>
    )
}

export default PopUp;