import React from 'react';
import './Navbar.css';
import Logo from '../Logo/Logo';
import {Link} from 'react-router-dom';

const Navbar = () => {
    return(
        <header className="navigation">
            <Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/"><Logo/></Link>
            <ul>
                <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/">Home</Link></li>
                <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/about">About</Link></li>
                <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/associates">Our Associates</Link></li>
                <li>Blog</li>
                <li>News</li>
                <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/join">Join Us</Link></li>
                <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to="/contact">Contact Us</Link></li>
            </ul>
        </header>
    )
}

export default Navbar;