import React from 'react';
import {Link} from 'react-router-dom';
import './Slider.css';

const Slider = () => {
    return(
        <div className="sliderContainer">
            <ul>
                <li>
                    <span>
                        "What matters to you matters to us."
                    </span>
                </li>
                <li>
                    <span>
                        "With us you are always one step ahead."
                    </span>
                </li>
                <li>
                    <span>
                        "We are not driven by fees, We are driven by results."
                    </span>
                </li>
                <li>
                    <div style={{"width":"70%"}}>
                        <span>Join hands with us and become a part of ILM</span>
                        <Link  className="joinBtn" to="/join">Join Us</Link>
                    </div>
                </li>
            </ul>
        </div>
    )
}

export default Slider;