import React from 'react';
import './Introduction.css';

const Introduction = () => {
    return(
        <section>
            <div className="wwaWrapper">
                <div className="wwaCol">
                    <div style={{"padding-bottom": "3rem"}}>
                        <p className="hText">Introduction</p>
                        <p className="pText">
                            We are client-centric Legal experts with high levels of professional standards and ethics. We function as a well-knit team of legal practitioners having diverse knowledge in the subject. We provide affordable, low cost legal service and exceptional legal advice to our clients.
                        </p>
                    </div>
                    <div>
                        <p className="hText">Our Mission</p>
                        <p className="pText">
                            Our mission is to provide our clients with knowledgeable, in-depth, legal guidance and service in a timely manner, at 
                            a reasonable price. We are dedicated to providing our clients with value and meeting their requirements. We strive to 
                            provide high quality legal services with integrity, professionalism and respect for our clients and the community. In 
                            doing this, we enjoy and strive for a collegial group practice while recognizing that our principal goal is to serve our 
                            clients effectively.
                        </p>
                        <p className="pText">
                            We also aim to educate law students and budding lawyers about litigation, practicality of law and justice system in the 
                            country. We achieve this by organising online and offline seminars, webinars, debates, internships and discussion programmes
                            at district, state and national level.
                        </p>
                    </div>
                </div>
                <div className="wwaCol">
                    <p className="hText">Our Values</p>
                    <p>
                        In pursuit of this mission, we hold to these values:
                    </p>
                    <ul className="valuesContainer">
                        <li>
                            <span>Excellence: </span>We strive to become one of the most sought-after providers of legal services in the region.
                        </li>
                        <li>
                            <span>Service: </span>We endeavor to meet or exceed the expectations of our clients in all aspects of their legal representation.
                        </li>
                        <li>
                            <span>Encouragement: </span>We promote a collegial atmosphere in which all individuals are encouraged to learn, improve and excel and to become leaders in the legal, business and civic communities.
                        </li>
                        <li>
                            <span>Strength: </span>We conduct our affairs to provide the financial strength and growth necessary to attract and keep the highest quality lawyers and staff.
                        </li>
                        <li>
                            <span>Diversity: </span>We encourage diversity among our members and respect for differences among us.
                        </li>
                        <li>
                            <span>Supportiveness: </span>We work daily to enhance the supportive attitude, common bond and collective sense of humor — the special working atmosphere.
                        </li>
                        <li>
                            <span>Community: </span>We will pursue our belief that individuals with a sense of family and community and with interests outside the practice of law are better for it.
                        </li>
                    </ul>        
                </div>
            </div>
        </section>
    )
}

export default Introduction;